.menu-item {
    min-height: 150px;
    padding: 0 40px;
    margin: 5px 10px;
    user-select: none;
    cursor: pointer;
    border: none;
}
.scroll-menu-arrow {
    padding: 1px;
    cursor: pointer;
}
.scroll-menu-arrow--disabled{
    display: none;
}
.menu-item-wrapper.active :focus {
    outline: none;
}
.menu-item-wrapper :focus{
    outline: none;
}
.img-product-alert {
    min-height: 150px;
    background-position: center; /* Center the image */
    background-repeat: no-repeat; /* Do not repeat the image */
    background-size: contain;
}
.no-center{
    text-align: initial !important;
}